}

.event-list tbody tr {
  cursor: pointer;
}

.event-list tbody tr:hover {
  background: #877abf;
}

.event-list tbody tr:hover td:last-child {
  background: #fff;
}

.event-list tbody tr:active {
  background: #9d8ed9;
}

.event-list tbody tr > :nth-child(1) {
  font-size: .7em;
}

@media screen and (max-width: 700px) {
  .event-list tr > :nth-child(4) {
    display: none;
  }

  .event-list tr:first-child .camera:before {
    content: "Cam";
  }
}

@media screen and (max-width: 465px) {
  .frame .react-calendar {
    width: 100%;
  }
}

@media screen and (max-width: 545px) {
  .event-list {
    width: 100%;
  }

  .event-list tr :nth-child(5) {
    display: none;
  }

  .event-list tr:first-child .camera:before {
    content: "Cam";
  }
}

